"use strict";
boann.controller('TraningenController', ['$scope', '$http', '$window', '$state', function($scope, $http, $window, $state) {

    console.log($state.$current.url.pattern.split("/")[1] + " = case name");
    console.log($state.router.globals.$current.views.mainpage.controller + "Controller is Loaded");

    var URI     =   controler.view + "traningen/index.php";
    var state   =   $state.$current.url.pattern.split("/")[1];

    switch(state){
        case "ifr_airport" :
            $scope.simbrief = function(data){
                if(data){
                    var VALUES = [{data:data, insert:true}];
                    $http.post(URI, VALUES, {params:{action:"simbrief"}}).then(function(data){
                        console.log(data.data);
                        $scope.form = data.data;
                    });
                }
            }
        break;
    }

    $scope.menuData =   '0';
    $scope.menuSet  =   'false';

    $scope.setMenu  =   function(data, show){
        switch(show){
            case "true" :
                $scope.menuSet  =   'false';
                $scope.menuData = '0';
            break;

            case "false" :
                $scope.menuSet  =   'true';
                $scope.menuData = data;
            break;
        }
    }

    $scope.getMenu  =   function(data){
        if(data === $scope.menuData){
            return $scope.menuData;
        }
    }

    $scope.menuSubData =   '0';
    $scope.menuSetSub  =   'false';

    $scope.setSubMenu  =   function(data, show){
        switch(show){
            case "true" :
                $scope.menuSetSub  =   'false';
                $scope.menuSubData = '0';
            break;

            case "false" :
                $scope.menuSetSub  =   'true';
                $scope.menuSubData = data;
            break;
        }
    }

    $scope.getSubMenu  =   function(data){
        if(data === $scope.menuSubData){
            return $scope.menuSubData;
        }
    }


}]);
