"use strict";
boann.controller('WallpaperController', ['$scope', '$http', '$window', '$state', function($scope, $http, $window, $state) {

    console.log($state.$current.url.pattern.split("/")[1] + " = case name");
    console.log($state.router.globals.$current.views.mainpage.controller + "Controller is Loaded");

    var URI     =   controler.view + "wallpaper/index.php";
    var state   =   $state.$current.url.pattern.split("/")[1];

    $http.get(URI, {params:{action:"wallpaper"}}).then(function(data){
        if(data.status === 200){            
            if(data.data){
                $scope.ImagesData = data.data;
            }           
        };
    });

    $scope.save    =   function(data){
       if(data){
            var VALUES = [{data:data}];
            $http.post(URI, VALUES, {params:{action:"saveImages"}}).then(function(data){
                if(data.status === 200){
                    console.log(data.data);
                    switch(data.data.data){
                        case "error" :
                            $scope.error = data.data.dataContent;                             
                        break;

                    case "success" :
                            $scope.success = data.data.dataContent;

                            setTimeout(function(){
                                location.reload();                       
                            }, 2000);
                        break;
                    }
                }
            });
       }     
    }

    $scope.close    =   function(){
        $http.get(URI, {params:{action:"DeleteUploaded"}}).then(function(data){
            if(data.status === 200){
                    console.log(data.data);
                switch(data.data.data){
                    case "success" :
                        $(".dropImg").fadeOut();
                        $(".dropzone-icon").delay(1000).fadeIn();
                    break;
                }
            }
        });
    }

    uploadImages(URI+"?action=uploadImages");

}]);