"use strict";
boann.controller('RegisterController', ['$scope', '$http', '$window', function ($scope, $http, $window) {
    var URI = controler.view + "register/index.php";


    $scope.submit = function(data){
        var VALUES = [{data:data}];
        $http.post(URI, VALUES, {params:{action:"register"}}).then(function(data){
            if(data.status === 200){
                console.log(data.data);
                switch(data.data.data){
                   case "success" :
                        swal("Well done!", data.data.dataContent, "success");
                   break;

                   case "error" :                        
                        swal("Oeps!", data.data.dataContent, "error");
                   break;
                }
            };
        });
    } 
    
}]);