"use strict";
boann.controller('HomeController', ['$scope', '$http', '$window', function($scope, $http, $window) {
        var URI       =   controler.view + "home/index.php";
        
        //chek when i live on twitch
        setInterval(get_twitch, 5000);
        get_twitch();

        function get_twitch(){
            $http.get(URI, {params:{action:"get_twitch",}}).then(function(data){
                if(data.status = 200){            
                    if(data.data){                       
                        $scope.twitch = data.data;                                    
                    }           
                };
            });
        }

        //get twitch stream
        new Twitch.Embed("harritvanbeek20", {
                width: 100 + "%",
                height: 497,
                channel: "harritvanbeek20",
                theme: "dark",
                layout: "video",
        });
                
}]);
