"use strict";
boann.controller('StarCitzenController', ['$scope', '$http', '$window', function($scope, $http, $window) {
        var URI     =   "./starcitzen_login/view/com_api/index.php";
        
        
            $http.get(URI, {params:{action:"get_orginisation"}}).then(function(data){
                if(data.status = 200){            
                    if(data.data){
                        $scope.org = data.data;                                     
                    }           
                };
            });

            $http.get(URI, {params:{action:"get_members"}}).then(function(data){
                if(data.status = 200){            
                    if(data.data){
                        $scope.members = data.data;                 
                    }           
                };
            }); 

        
            setInterval(GetScDate, 1000/6);
            function GetScDate() {
                var date = moment.utc('1899-12-30', 'YYYY-MM-DD');
                var utcMoment = moment.utc();
                var difDays = utcMoment.diff(date, 'days', true);

                var calculation = 383507.0 + ((difDays - 43831.0) * 6.0);

                date.add(calculation * 86400, 's');            
                document.getElementById('scTime').innerHTML = date.utc().format("ddd MMM D HH:mm:ss YYYY"); // ddd MMM D HH:mm:ss YYYY                          
                document.getElementById('scDays').innerHTML = date.utc().format("D");
                document.getElementById('scHouwer').innerHTML = date.utc().format("HH");
                document.getElementById('scMin').innerHTML = date.utc().format("mm");            
            }

            setInterval(GetRealDate, 1000);
            function GetRealDate() {
                var now = moment(new Date());            
                document.getElementById('realTime').innerHTML = now.format("ddd MMM D HH:mm:ss YYYY"); // ddd MMM D HH:mm:ss YYYY
            } 
         
}]);