"use strict";
boannA.controller('FlichtSimController', ['$scope', '$http', '$window', function($scope, $http, $window) {
        //config flight map        
        var getZoom       = 4;
        var setZoom       = 7;
        var refresh       = 35000;
        var who_json_url  = "get_flights_json";
        var URI           = "../../libraries/callback/callback_msfs.php";

        //check flicht
        setInterval(get_flights, 1000);
        get_flights();

        function get_flights(){
            if(localStorage.getItem("callsign") !== null || localStorage.getItem("callsign") !== "N/A"){
                $http.get(URI, {params:{action:"callsign", callsing:localStorage.getItem("callsign")}}).then(function(data){
                    if(data.status = 200){
                        if(data.data){
                            $scope.flight = data.data;
                            progressBar();
                        }
                    };
                });
            }
        }

        $scope.close    =   function(data){
                $("#flight-info-wrapper").fadeOut();
                if(data === localStorage.getItem("callsign")){
                    localStorage.removeItem("callsign")
                }
        }

        function progressBar(){
            var totalDistance = $scope.flight.currentLocation.distance_remaining + $scope.flight.currentLocation.distance_flown;
            var progress      = $scope.flight.currentLocation.distance_flown / totalDistance;
            $scope.progress =   progress = + progress.toFixed(2).split(".")[1];            
         }

         //utc time
         setInterval(updateTime, 1000);         
         function updateTime() {
            var currentTime  = new Date();            
            var getTime      = currentTime.toLocaleString('en-GB', {timeZone: 'Europe/London' }).split(',')[1].split(':');
    
            $scope.setTime   = getTime["0"]+':'+getTime["1"];             
         }

}]);
